.home-left {
    flex: 25;
    display: flex;
    flex-direction: column;
    margin-right: 25px;
    height: 100%;
}

.home-left-top {
    flex: 15;
    margin-bottom: 15px;
}

.home-visitCountWrap {
    display: flex;
    height: calc(100% - 35px);
    flex-direction: column;
}

.home-VisitCount-chart {
    flex: 15;
    margin-bottom: 15px;
}

.todayVisit {
    flex: 5;
    display: flex;
    flex-direction: row;
    padding-left: 20px;
    color: #fff;
}

.todayVisit-h {
    flex: 2;
    font-size: 1.3rem;
}

.todayVisitWrap {
    flex: 10;
    display: flex;
    flex-direction: row;
    padding-bottom: 20px;
    font-size: 1.3rem;
    font-weight: bold;
}

.home-visitWrap-div {
    flex: 1;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    height: 100%;
    border-left: 10px solid #3398DB;
    padding: 0px 10px;
    box-sizing: border-box;
}

.home-visitWrap-div div {
    flex: 1;
    display: flex;
    height: 100%;
    line-height: 100%;
    align-items: center;
}

.home-left-bottom {
    flex: 10;
}


.home-right {
    flex:15;
    display: flex;
    flex-direction: column;
    height: 100%;
}

.home-right-top {
    flex: 3;
}

.home-right-center {
    flex: 5;
    margin: 15px 0;
}

.home-right-center .m-h {
    position: relative;
    background-color: rgba(204, 0, 0, 0.41);
}

.home-right-center .m-h div {
    position: absolute;
    right: 4%;
    display: flex;
    height: 100%;
    align-items: center;
}

.home-right-center .m-h div a {
    color: #fff;
    font-size: 0.9rem;
    cursor: pointer;
}

.home-right-bottom {
    flex: 5;
}

.home-table {
    width: 100%;
    height: 100%;
    text-align: center;
    color: #fff;
    border-collapse: collapse;
}