.timeSelect {
    position: relative;
    padding: 0 10px;
    margin-bottom: -5px;
    margin-top: 5px;
    width: 100%;
    height: 40px;
    box-sizing: border-box;
    display: flex;
    justify-content: flex-start;
}

.timeselect-h {
    display: flex;
    align-items: center;
    margin-left: 40px;
    color: #fff;
    font-size: 1.1rem;
    height: 100%;
}

.timeselect-bar {
    display: flex;
    align-items: center;
    height: 40px;
    color: #fff;
    font-size: 20px;
}

.timeselect-bar input {
    height: 28px;
    background-color: rgb(98, 104, 113);
    border: none;
    color: #fff;
    margin: 0 18px;
}

.timeselect-bar div {
    color: #fff;
    height: 28px;
    line-height: 28px;
    width: 28px;
    text-align: center;
    background-color: #27293D;
    cursor: pointer;
    font-size: 20px;
    padding: 0 8px;
    transition: all 0.3s;
}

.timeselect-bar div:hover {
    padding: 0 10px;
    background-color: #3398DB;
}