html {
    height: 100%;
}

body {
    background-color: rgba(9, 12, 43, 0.95);
    height: 100%;
}

#title_left {
    float:left;
    width: 33%;
    height: 6%;
    background-color:transparent;
    transform: skewX(50deg);
    border-top: solid 2px rgb(255, 255, 255, 0.75);
    border-right: solid 10px rgb(255, 255, 255, 0.75);
}

#title_right {
    float:right;
    width: 33%;
    height: 6%;
    background-color: transparent;
    transform: skewX(-50deg);
    border-top: solid 2px rgb(255, 255, 255, 0.75);
    border-left: solid 10px rgb(255, 255, 255, 0.75);
}

#system_logo {
    position: absolute;
    width: 7.9%;
    top: 8%;
    left: 23%;
}

#system_name div {
    position: absolute;
    left: 33%;
    top: 3%;
    text-align: center;
    font-size: 2vw;
}

#system_name {
    position: relative;
    color: rgb(255, 255, 255, 1);
    width: 30%;
    height: 10%;
    margin: 0 auto;
}

#clock {
    width:40%;
    height:2%;
    margin:0 auto;
    color: rgb(255, 255, 255, 0.9);
    text-align: center;
    margin-top: -1%;
}

#score {
    position: absolute;
    float: left;
    margin: 0 auto;
    color: rgb(255, 255, 255, 0.9);
    width: 15%;
    height: 50px;
    left: 5%;
    top: 10px;
}

#score_1 {
    font-size: 1.2em;
}

#score_2 {
    font-size: 1.8em;
    font-weight: bold;
}

#map {
    width: 60%;
    height: 60%;
    margin:0 auto;
}

#attacks_rose {
    width: 30%;
    height: 35%;
    position: absolute;
    left: 1%;
    top: 25%;
}

#requests_line
 {
    width: 30%;
    height:40%;
    position: absolute;
    left: 1%;
    top: 63%;
}

#ip_top_bar {
    width: 30%;
    height: 40%;
    position: absolute;
    right: 2%;
    top: 18%;
}

#requests_pie {
    width: 30%;
    height: 35%;
    position: absolute;
    right: 1%;
    top: 65%;
}

#attacks_info {
    width: 40%;
    height: 23%;
    margin: 0 auto;
    margin-top: 1%;
    color: rgba(255, 255, 255, 0.75);
    font-size: 0.5em;
}

#sec_status {
    width: 30%;
    height: 200px;
    position: absolute;
    right: 1%;
    top: 5%;
}

#today {
    width: 30%;
    height: 15%;
    color: rgba(255, 255, 255, 0.75);
}