.analysis-home {
    height: calc(100% - 40px);
    padding: 10px 10px;
    min-height: 500px;
    background-color: #1E1E2F;
}

.analysis-left {
    flex: 22;
    display: flex;
    flex-direction: column;
    margin-right: 15px;
    height: 100%;
}

.analysis-right {
    flex:15;
    display: flex;
    flex-direction: column;
    height: 100%;
}

.analysis-topWrap {
    display: flex;
    flex-direction: row;
    height: calc(100% - 35px);
    box-sizing: border-box;
    color: #fff;
}

.analysis-column-top {
    flex: 11;
}

.analysis-AttackSourceIP-chart {
    flex: 10;
    height: 100%;
}

.IPTop {
    flex: 4;
    height: 100%;
}

.topHead {
    height: 35px;
    line-height: 35px;
    text-align: center;
    font-size: 1.3rem;
    font-weight: 600;
    border-bottom: 1px solid #797979;
}

.topMain {
    height: calc(100% - 35px);
    display: flex;
    flex-direction: column;
    font-size: 1.3rem;
}

.topMain div {
    flex: 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 10px;
}

.AttackIPRoot-chart {
    flex: 10;
    height: 100%;
}

.rootTop {
    flex: 5;
    height: 100%;
}

.analysis-column-bottom {
    margin-top: 10px;
    flex: 10;
}

.analysis-AttackType-chart {
    flex: 10;
    height: 100%;
}

.typeTop {
    flex: 5;
    height: 100%;
}



/* attackWords */
.wordBlock {
    flex: 1;
    height: 100%;
    display: flex;
    flex-direction: column;
    color: #fff;
    overflow: hidden;
}

.wordBlock .blockHead {
    display: flex;
    height: 35px;
    box-sizing: border-box;
    justify-content: center;
    align-items: center;
    font-size: 20px;
    background-color: #40a9ff;
}

.wordBlock .blockBody {
    position: relative;
    height: calc(100% - 35px);
}

.blockBody .circle {
    position: absolute;
    left: calc(50% - 19px);
    top: calc(50% - 19px);
    width: 38px;
    height: 38px;
    border-radius: 50%;
    border: 2px solid #52c41a;
    box-sizing: border-box;
    z-index: 1;
}

.blockBody .copyCircle {
    position: absolute;
    left: calc(50% - 17px);
    top: calc(50% - 17px);
    width: 34px;
    height: 34px;
    border-radius: 50%;
    background-color: #27293D;
    box-sizing: border-box;
    z-index: 9;
}

.blockBody .solid {
    position: absolute;
    left: 18px;
    top: 18px;
    width: 62px;
    height: 0;
    transform-origin: 0;
    border-top: 1px solid #40a9ff;
    box-sizing: border-box;
    z-index: -2;
}

.blockBody .words {
    position: absolute;
    left: -22px;
    top: -88px;
    width: 80px;
    transform-origin: center 105px;
    word-break: break-word;
    text-align: center;
    color: #52c41a;
    box-sizing: border-box;
    cursor: default;
}