html, body {
  margin: 0;
  padding: 0;
  height: 100%;
  width: 100%;
  /* font-family: Microsoft Yahei; */
  box-sizing: border-box;
  background-color: #1E1E2F !important;
  min-width: 1250px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'PingFang SC', 'Hiragino Sans GB',
  'Microsoft YaHei', 'Helvetica Neue', Helvetica, Arial, sans-serif, 'Apple Color Emoji',
  'Segoe UI Emoji', 'Segoe UI Symbol';
}

ul {
  margin: 0;
  padding: 0;
}

li {
  list-style-type: none;
}

a {
  text-decoration: none;
}


header {
  top: 0;
  left: 0;
  width: 100%;
  z-index: 999;
  color: #fff;
  background-color: #27293D;
  clear: both;
  border-bottom: 1px solid #e4e4e4;
}

.head-wrap {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  height: 70px;
}

.head-logo {
  display: flex;
  padding: 0 0 0 10px;
  align-self: center;
}

.head-logo img {
  width: 50px;
  height: 50px;
}

.head-h {
  display: flex;
  flex-direction: column;
  align-self: center;
  color: #fff;
  padding-left: 20px;
}

.zh {
  font-size: 1.75rem;
}

.en {
  font-size: 0.8rem;
}

.head-menu {
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 0 55px;
  font-size: 1.55rem;
}

.head-menu a {
  padding: 0 25px;
  color: #fff;
}

.head-menu a:hover {
  color: #40a9ff;
}

.menu-1 {
  color: #40a9ff!important;
}

.head-infor {
  display: flex;
  align-items: center;
  font-size: 1.2rem;
  padding-right: 30px;
  font-family: Georgia;
}

.head-pers img {
  width: 45px;
}

.news {
  position: absolute;
    font-size: 1rem;
    background-color: red;
    border-radius: 50px;
    height: 20px;
    text-align: center;
    line-height: 18px;
    margin-top: -7px;
    margin-left: -10px;
    color: #fff;
    padding: 0 5px;
}

/*main*/
.g-home {
  padding: 10px 10px;
  background-color: #1E1E2F;
  height: calc(100% - 92px);
  min-height: 500px;
}

.m-main {
  display: flex;
  height: 100%;
}

.table {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  height: calc(100% - 35px);
  background-color: #27293D;
}

.m-h {
  width: 100%;
  height: 35px;
  border-bottom: 1px solid #ccc;
  display: flex;
  box-sizing: border-box;
  color: #fff;
}

.m-h span {
  height: 35px;
  line-height: 35px;
  color: #fff;
}

.m-h b {
  display: flex;
  align-items: center;
  margin-left: 20px;
  font-size: 1.3rem;
  height: 100%;
}

.m-left > div, .m-right > div, .m-top > div, .m-bottom > div {
  background-color: #27293D;
}

.home-left > div, .home-right > div {
  background-color: #27293D;
}
.detect-left > div, .detect-right > div {
  background-color: #27293D;
}
.analysis-left > div, .analysis-right > div {
  background-color: #27293D;
}
.state-top > div, .state-bottom > div {
  background-color: #27293D;
}

.diffTrColor {
  background-color: #383A4C;
}

.topMainNull {
  display: flex;
  justify-content: center;
  height: calc(100% - 36px);
  align-items: center;
  font-size: 1.5rem;
  color: #fff;
  background-color: #27293D;
}










