.state-home {
    height: calc(100% - 40px);
    padding: 10px 10px;
    min-height: 500px;
    background-color: #1E1E2F;
}

.state-main {
    flex-direction: column;
    height: 100%;
    display: flex;
}

.state-top {
    flex: 11;
    display: flex;
    flex-direction: row;
}

.state-bottom {
    flex: 10;
    display: flex;
    margin-top: 10px;
}

.state-row-left {
    flex: 5;
    margin-right: 15px;
}

.m-state-table {
    color: #fff;
    border-collapse: collapse;
    width: 100%;
    height: 100%;
    text-align: center;
}

.m-state-table tr td:first-child {
    color: #fff;
    font-weight: bold;
}

.m-state-table tr:first-child {
    color: #fff;
    font-weight: bold;
}

.state-row-right {
    flex: 10;
}

.state-row-right img {
    width: 25px;
}

.HistoryEvaluate {
    flex-grow: 1;
}

.StateEvaluate .m-h {
    background-color: rgba(204, 0, 0, 0.41);
}