.m-h {
    position: relative;
}

.detect-left {
    flex: 15;
    display: flex;
    flex-direction: column;
    margin-right: 15px;
    height: 100%;
}

.detect-table {
    display: flex;
    align-items: flex-start;
}

.m-detect-table {
    color: #fff;
    border-collapse: collapse;
    width: 100%;
    height: 100%;
    text-align: center;
}

.m-detect-table img {
    width: 25px;
}

.m-detect-h-right {
    position: absolute;
    right: 15px;
    height: 100%;
    display: flex;
    align-items: center;
}

.m-detect-h-right input[type="search"] {
    background-color: #2f2f3b;
    color: #fff;
    border: none;
    border-radius: 5px;
    padding: 5px 8px;
    font-size: 1rem;
    -webkit-box-shadow: none;
    box-shadow: none;
    outline: none;
}

.m-detect-h-right input::placeholder {
    color: #ccc;
}

.m-detect-h-right div {
    width: 25px;
    height: 25px;
    padding-right: 5px;
}

.m-detect-h-right div img {
    width: 100%;
    height: 100%;
}

.attackDetectTable {
    width: 100%;
    height: calc(100% - 35px - 56px);
    color: #fff;
    text-align: center;
    border-collapse: collapse;
    
}

.attackHistoryTable tr {
    height: 16.5%;
}

.attackHistoryTable tbody tr {
    cursor: pointer;
}

.detectResultTable tbody tr {
    height: 20%;
}

.attackDetectTable th {
    padding: 10px 18px;
    font-size: 2.5vh;
}

.attackDetectTable td {
    padding: 10px 18px;
    font-size: 2.5vh;  
}

.pagination-footer {
    height: 56px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    padding: 0 50px;
    color: #fff;
}

footer .pagination {
    margin: 0;
    font-size: 14px;
    list-style: none;
    /* display: inline-block; */
}

.pagination li div {
    height: 24px;
    line-height: 24px;
    cursor: pointer;
    display: flex;
    align-items: center;
}

.pagination img {
    height: 18px;
}

.pagination-prev {
    display: inline-block;
    height: 24px;
    line-height: 24px;
    vertical-align: top;
    margin-right: 8px;
}

.pagination-pager {
    display: inline-block;
    height: 24px;
    margin-right: 8px;
}

.pagination-pager span {
    margin: 0 10px 0 5px;
}

.pagination-pager input {
    height: 100%;
    width: 75px;
    border-radius: 4px;
    outline: none;
    padding: 0 5px;
    text-align: center;
    border: none;
    margin-right: 8px;
    box-sizing: border-box;
    border: 1px solid #d9d9d9;
    transition: border .3s;
    background-color: #e4e4e4;
    color: #333;
}

.pagination-pager input:hover {
    border: 1px solid cornflowerblue;
}

.pagination-next {
    display: inline-block;
    height: 24px;
    line-height: 24px;
    vertical-align: top;
}

.AttackHistory-th {
    display: flex;
    flex-direction: row;
    width: 100%;
    height: 30px;
    line-height: 30px;
    font-size: 17px;
    font-weight: 700;
    color: #fff;
    box-sizing: border-box;
    text-align: center;
}

.detect-right {
    flex: 20;
    display: flex;
    flex-direction: column;
    height: 100%;
}

.detect-tfooter {
    position: absolute;
    bottom: 0;
    right: 15px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 35px;
    color: #fff;
}

.left-AttackHistory {
    flex: 5;
    margin-bottom: 10px;
    position: relative;
}

.left-TestResult {
    flex: 4;
    position: relative;
}

.right-AttackDetail {
    flex: 1;
    height: 100%;
}

.detailMain {
    display: flex;
    width: 100%;
    height: calc(100% - 35px);
}

.detailNav {
    flex: 3;
}

.detailWrap {
    width: 100%;
    display: flex;
    flex-direction: column;
    padding-top: 13px;
}

.detailWrap div {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 50px;
    margin: 18px 0;
    background-color: #383A4C;
    cursor: pointer;
    color: #fff;
    cursor: pointer;
}

.detailWrap .detailOn {
    background-color: #40a9ff;
}

.detailTable {
    flex: 20;
    height: 100%;
    border-left: 1px solid #ccc;
    box-sizing: border-box;
    padding: 20px 25px;
    color: #fff;
    overflow: auto;
    font-size: 1.2rem;
}

.detailTable li {
    line-height: 5rem;
}

.detailTable .li-head {
    width: 120px;
    font-weight: bold;
    display: inline-block;
    text-align: justify;
    text-align-last: justify;
}

.detailTable .li-main {
    display: inline-block;
    margin-left: 25px;
}

.detailTable .dropdown {
    position: relative;
    display: inline-block;
    margin-right: 25px;
    vertical-align: text-top;
}

.dropdown .dropdown-content {
    line-height: 3rem;
    border: 1px solid #ccc;
}

.dropdown .dropdown-content div {
    padding: 0 16px;
    background-color: #4A5C6A;
    cursor: pointer;
    text-align: left;
}

.dropdown .dropdown-content div:first-child {
    background-color: transparent;
}

.dropdown .dropdown-content div:hover {
    background-color: transparent;
}

.detailInfor input {
    height: 100%;
    border-radius: 4px;
    outline: none;
    padding: 0 5px;
    text-align: center;
    border: none;
    margin-right: 18px;
    box-sizing: border-box;
    border: 1px solid #d9d9d9;
    transition: border .3s;
    background-color: #e4e4e4;
    color: #fff;
}

.detailInfor input:hover {
    border: 1px solid cornflowerblue;
}

.attackDetail-bt {
    display: inline-block;
    cursor: pointer;
    background-color: #40a9ff;
    line-height: 2rem;
    border-radius: 4px;
    padding: 0 6px;
    color: #fff;
    font-size: 0.8rem;
    vertical-align: middle;
    margin-top: -8px;
}

.detailTable .li-url {
    border-radius: 4px;
    background: #fff;
    vertical-align: bottom;
    padding: 5px 10px;
    font-size: 1.5rem;
    width: 100%;
    background-color: transparent;
    /* border: 1px solid #1295db7e; */
    max-height: 180px;
    max-width: 934px;
    line-height: 1.5rem;
    overflow-y: auto;
    word-wrap: break-word;
    box-sizing: border-box;
}